	// THIRD SCREEN

	.third-screen {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.7);
			opacity: 0;
			transition: opacity 0.5s;
		}

		&--filter::before {
			opacity: 1;
		}

		&__video {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			overflow: hidden;
			z-index: 1;
			video {
				transform: scale(1.3);
				transform: scale(1.3);
				height: 100%;
				width: 100%;
			}
		}

		&__content {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__text-block {
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: calc($index * 1.5);
			position: absolute;
			opacity: 0;
			max-width: 1072px;

			h2 {
				// font-size: calc($index * .9);
				@include fontSize(34, 22, 1800);
				font-weight: 500;
			}

			p {
				font-size: calc($index * 0.7);
				line-height: 161.5%;
				letter-spacing: 0.4px;
			}

			h2,
			p {
				color: #fff;
				text-align: center;
				// transition: 1s;
			}
		}

		&__button {
			position: absolute;
			bottom: 20%;
			padding: 10px 0;
			align-items: center;
			background-color: #fff;
			text-transform: none;
			transition: 0.5s;

			p {
				background: $purple-to-pink;
				background-clip: text;
				-webkit-text-fill-color: transparent;
				font-size: calc($index * 0.45);
				font-weight: bold;
				pointer-events: none;
			}

			img {
				pointer-events: none;
			}
		}
	}

	@media (max-width: 1000px) {
