	// Четвертый экран

	.four-screen {
		background-color: #fdfdfd;
		height: 100vh;

		&__bcg {
			background: url(../../img/Product-page/4-screen-bg1.png) no-repeat center center / cover;
			filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.2));
			border-radius: 40px;
			overflow: hidden;
		}

		&__rama {
			position: absolute;
			// max-width: calc($index * 18);
			left: 50%;
			transform: translateX(-50%);
			// bottom: calc((1vw + 1vh) * -1.5);
			z-index: -1;
			height: 98%;
			bottom: -30px;
			overflow: hidden;

			&::before {
				/* создайте псевдоэлемент ::before */
				content: "";
				display: block;
				// padding-bottom: 56.25%;
				/* установите соотношение сторон 16:9 */
			}

			&-blur {
				position: absolute;
				top: 16px;
				width: 100%;
				height: 100%;
				backdrop-filter: blur(2.5px);
				background-color: rgba(113, 99, 77, 0.35);
				z-index: -1;
				transition: backdrop-filter 0.5s ease-in-out;

				&--full-blur {
					// transition: 1 all;
					backdrop-filter: blur(18px);
				}
			}

			img {
				height: 100%;
			}
		}

		&__rama-item {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-45%, -45%);
			width: 23%;
		}

		&__buttons {
			position: absolute;
			display: flex;
			flex-direction: column;
			gap: 50px;
			max-width: calc((1vw + 1vh) * 9);
			width: 100%;
			top: calc(50% + 55px);
			transform: translateY(-50%);
			right: 10%;
		}

		&__button {
			padding: 10px 0;
			align-items: center;
			background-color: #3e304e;
			text-transform: none;
			box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.2);

			&--active {
				border-radius: 20px;
				background: linear-gradient(90deg, #3926ad 0%, #c367d6 100%) !important;
				/* нажатая кнопка */
				box-shadow: 0px 3px 7px 4px rgba(23, 4, 47, 0.25),
					0px -3px 4px 0px rgba(239, 175, 255, 0.19);
			}

			p {
				color: #fff;
				font-size: calc($index * 0.45);
				font-weight: normal;
				letter-spacing: 0.26px;
			}
		}
	}

	@media (max-width: 1400px) {
		.four-screen {
			&__buttons {
				right: 5%;
			}
		}
	}

	@media (max-width: 1146px) {
